@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: "Nanum Gothic", sans-serif;
  background: #536976; /* fallback for old browsers */
  /* background: -webkit-linear-gradient(
    to top,
    #292e49,
    #536976
  ); Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to bottom,
    #292e49,
    #395461,
    #292e49
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  /* overflow-x: hidden; */
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.main {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

body::-webkit-scrollbar {
  width: 10px; /* width of the entire scrollbar */
}

body::-webkit-scrollbar-track {
  background: linear-gradient(to bottom, #292e49, #395461);
}

body::-webkit-scrollbar-thumb {
  background: #282c34;
  background-image: linear-gradient(#6498e6, #4d747e);
  border-radius: 20px; /* roundness of the scroll thumb */
}

main {
  align-items: center;
  text-align: center;
}

nav {
  display: flex;
  position: fixed;
  justify-content: space-around;
  width: 20%;
  height: 100vh;
  /* background-color: #282c34; */
  flex-direction: column;
  align-items: center;
}

.nav-links {
  position: relative;
  text-align: right;
  padding-right: 10%;
  justify-content: space-around;
  display: flex;
  flex-direction: column;
  width: 70%;
  height: 60%;
  text-transform: uppercase;
}

.nav-links-link {
  color: rgb(163, 186, 202);
  letter-spacing: 5px;
}

.nav-links-link:hover {
  color: white;
  font-weight: bold;
}

.nav-links-link:active {
  color: white;
  font-weight: bold;
}

.nav-links li {
  list-style: none;
}

.nav-underline {
  background-image: linear-gradient(to right, #4259a6, #34b0cf);
  height: 1px;
  padding-top: 0px;
}

.burger {
  position: absolute;
  top: 10px;
  left: 10px;
  display: none;
}

.burger div {
  cursor: pointer;
  width: 25px;
  height: 3px;
  background-color: rgb(255, 255, 255);
  margin: 5px;
}

@media screen and (max-width: 768px) {
  body {
    overflow-x: hidden;
  }
  nav {
    transform: translateX(-100%);
    width: 100vw;
    height: 100vh;
  }

  .nav-links {
    transform: translateX(-100%);
    position: relative;
    left: 0px;

    display: flex;
    flex-direction: column;
    align-items: center;
    width: 50%;
    transition: transform 0.5s ease-in;
  }
  .nav-links li {
    opacity: 0;
  }
  .burger {
    display: block;
  }
}

.nav-active {
  transform: translateX(0%);
}

.nav-active .nav-links li {
  opacity: 1;
}

.main {
  margin-left: 20%;
  text-align: center;
}

.home-section {
  height: 100vh;
  width: 80%;
  position: relative;
}

img {
  pointer-events: none;
}

.icon {
  color: white;
  margin: 10px;
  cursor: pointer;
}

.icon:hover {
  color: #79c9ff;
  transition: all 0.4s ease;
}

.scroll-footer {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 30px;
  font-size: 0.6em;
}

@media screen and (max-height: 500px) {
  .scroll-footer {
    display: none;
  }
}

@media screen and (max-width: 768px) {
  .main {
    margin-left: 0px;
  }
}

.message-box {
  position: relative;
  text-align: left;
  padding: 20px;
  width: 50vw;
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  font-size: 0.8em;
  line-height: 150%;
}

.skills-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
}

.skills-box {
  position: relative;
  width: 10em;
  height: 11em;
  background-color: rgba(30, 33, 43, 0.5);
  border-radius: 20px;
  padding: 20px;
  margin: 10px;
}

.skills-box:hover {
  transform: translateY(20px);
}

.skills-box h2 {
  font-size: 1em;
}

.skills-box p {
  font-size: 0.8em;
}

#skills button {
  background-color: rgba(30, 33, 43, 0);
  border: none;
  border-radius: 30px;
  box-shadow: 0px 0px 10px #34b0cf;
  margin-top: 30px;
  padding: 20px;
  color: white;
  transition-duration: 0.4s;
  cursor: pointer;
  outline: 0;
}

#skills button:hover {
  letter-spacing: 5px;
  box-shadow: 0px 0px 20px rgb(108, 210, 250);
  border: none;
}

.skill-list {
  text-align: left;
  font-size: large;
}

.skill-list-active {
  transition: width 2s linear 1s;
}

.skill-category {
  font-style: bold;
}

div::-webkit-scrollbar {
  display: none;
}

.project-container {
  width: 70vw;
  align-items: center;
  justify-content: space-evenly;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.project-preview {
  position: relative;
  width: 20rem;
  height: 15rem;
  background-color: rgba(25, 26, 32, 0.5);
  border-radius: 20px;
  margin: 0.5rem;
  padding: 30px;
  transition-duration: 0.7s;
}

.project-preview:hover {
  cursor: pointer;
  box-shadow: 0px 0px 10px 5px #34b0cf;
}

.project-preview img {
  width: 100%;
  border-radius: 10px;
  margin-bottom: 1rem;
}

.modal-content {
  justify-content: space-evenly;
  display: flex;
  flex-direction: column;
}

.project-slideshow {
}

.project-slideshow img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 80%;
  border-radius: 20px;
}

.button-container {
  margin-top: 1px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}

.link-button {
  font-size: medium;
  background-color: rgba(30, 33, 43, 0);
  border: none;
  border-radius: 30px;
  box-shadow: 0px 0px 10px #ffffff;
  margin-top: 30px;
  padding: 10px;
  color: rgb(255, 255, 255);
  transition-duration: 0.4s;
  cursor: pointer;
  outline: 0;
}

.link-button:hover {
  letter-spacing: 2px;
  box-shadow: 0px 0px 20px rgb(255, 255, 255);
  border: none;
}
.ReactModal__Overlay {
  opacity: 0;
  transition: opacity 200ms ease-in-out;
}

.ReactModal__Overlay--after-open {
  opacity: 1;
}

.ReactModal__Overlay--before-close {
  opacity: 0;
}

@media screen and (max-width: 768px) {
}

.contact-container {
  width: 60vw;
  justify-content: space-evenly;
  align-items: center;
}

.received-container {
  display: flex;
  flex-direction: row;
  text-align: left;
}

.received-message {
  background-color: rgba(31, 30, 37, 0.4);
  padding: 20px;
  border-radius: 30px;
  font-size: large;
}

.sent-message {
  background-color: rgb(62, 147, 168);
  padding: 30px;
  border-radius: 30px;
  text-align: left;
  font-size: large;
  width: 90%;
}

a {
  color: rgb(30, 158, 190);
  text-decoration: none;
}

.input {
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 93%;
  border-radius: 20px;
  background-color: rgba(28, 28, 31, 0.7);
}

-webkit-input-placeholder {
  color: rgb(114, 114, 114);
}

.input-field {
  background-color: rgba(37, 37, 44, 0);
  color: white;
  width: 90%;
  height: 2em;
  padding: 10px;
  border-color: rgb(59, 59, 59);
  border-style: none none solid none;
  border-width: 1px;
  font-family: inherit;
}

textarea {
  resize: none;
}

.input-field:focus {
  border-color: rgb(30, 158, 190);
  outline: none;
}

.message-field {
  height: 10rem;
}

.input button {
  height: 40px;
  width: 40px;
}

.send-container {
  position: relative;
}

.send-icon {
  position: absolute;
  bottom: 0px;
  right: 0px;
}

.error-message {
  color: rgb(255, 147, 147);
  font-size: medium;
  text-align: left;
  margin: 10px;
}

@media screen and (max-width: 768px) {
  .contact-container {
    width: 80vw;
  }
  .input {
    width: 90%;
  }
}

